.Cell {
  height: 6rem;
  width: 6rem;
  background-color: var(--color-cell-bg);
  transition: background-color 0.3s ease;
  border-radius: var(--border-radius);
}

.Cell-lit {
  background-color: var(--color-cell-fill);
}

@media (max-width: 768px) {
  .Cell {
    height: 4rem;
    width: 4rem;
  }
}