:root {

  --border-radius: 5px;

  /* spacing */
  --small-padding: 10px;
  --medium-padding: 20px;
  --large--padding: 30px;

  /* color */
  --color-white: white;
  --color-cell-fill: #00dd0b;
  --color-cell-bg: rgba(0, 46, 0, 0.7);;

}

@font-face {
  font-family: 'PT Sans';
  src: url(/static/media/PTSans-Bold.e9c53d8c.woff) format('woff');
  font-weight: bold;
  font-style: normal;
}

@font-face {
  font-family: 'PT Sans';
  src: url(/static/media/PTSans-Regular.3f40ccfd.woff) format('woff');
  font-weight: normal;
  font-style: normal;
}

* {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0 20px;
  padding: 0 var(--medium-padding);
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: "PT Sans", "Verdana", sans-serif;
  color: white;
  color: var(--color-white);
  text-align: center;
  background-image: url(/static/media/bg.4099acba.jpg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  -webkit-backdrop-filter: blur(10px);
  backdrop-filter: blur(10px);
  min-height: 100vh;
}

@media (max-width: 375px) {
    body {
      padding: 10px !important;
      padding: var(--small-padding) !important;
    }
}


.Cell {
  height: 6rem;
  width: 6rem;
  background-color: var(--color-cell-bg);
  transition: background-color 0.3s ease;
  border-radius: var(--border-radius);
}

.Cell-lit {
  background-color: var(--color-cell-fill);
}

@media (max-width: 768px) {
  .Cell {
    height: 4rem;
    width: 4rem;
  }
}
.Board {
  margin: 0 auto;
  box-shadow: 0 0 50px #ffffff, -10px 0 80px rgb(0, 255, 157), 10px 0 80px rgb(179, 255, 0);
  border-radius: var(--border-radius);
}

.Board-title{
  display:flex;
  justify-content: center;
  margin: var(--medium-padding) auto;
}

.container {
  display: table-cell;
  text-align: center;
  vertical-align: middle;
}
.title {
  font-family: 'Arial', sans-serif; 
  font-size: 60px; 
  text-transform: uppercase;
  color: var(--color-white); 
  letter-spacing: 2px; 
  text-shadow: 0 0 10px rgb(217, 255, 0), 0 0 20px rgb(0, 201, 0), 0 0 30px rgb(255, 145, 0); 
  transition: text-shadow 0.3s ease-in-out; 
  -webkit-animation: neonGlow 1.5s infinite alternate; 
          animation: neonGlow 1.5s infinite alternate;
  padding: 40px;
  margin-bottom: 4rem;
  background-color: #86868673;
  border-radius: var(--border-radius);
}

@-webkit-keyframes neonGlow {
  0% {
    text-shadow: 0 0 10px rgb(217, 255, 0), 0 0 20px rgb(0, 201, 0), 0 0 30px rgb(255, 145, 0);
  }
  100% {
    text-shadow: 0 0 20px rgb(217, 255, 0), 0 0 40px rgb(0, 201, 0), 0 0 60px rgb(255, 145, 0);
  }
}

@keyframes neonGlow {
  0% {
    text-shadow: 0 0 10px rgb(217, 255, 0), 0 0 20px rgb(0, 201, 0), 0 0 30px rgb(255, 145, 0);
  }
  100% {
    text-shadow: 0 0 20px rgb(217, 255, 0), 0 0 40px rgb(0, 201, 0), 0 0 60px rgb(255, 145, 0);
  }
}

.title:hover {
  text-shadow: 0 0 20px rgb(217, 255, 0), 0 0 40px rgb(0, 201, 0), 0 0 60px rgb(255, 145, 0); /* Тінь під час ховеру */
}

.privacy {
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: relative;
  padding: var(--large--padding);
}

.link-btn {
  padding: var(--small-padding) var(--medium-padding);
  border-radius: var(--border-radius);
  color: var(--color-white);
  text-transform: uppercase;
  text-decoration: none;
  font-size: 16px;
  background: rgb(2,0,36);
  background: linear-gradient(90deg, rgb(216, 245, 0) 0%, rgb(6, 49, 17) 41%, rgb(0, 255, 128) 100%);
  background-size: 400%;
  background-position: center;
}

@media (max-width: 768px) {
  .title {
    font-size: 40px;
  }
}
@media (max-width: 375px) {
  .title {
    font-size: 30px;
    padding: var(--large--padding);
    margin-bottom: 5vh;
  }
  .privacy {
   flex-direction: column;
  }
  .link-btn:first-child {
    margin-bottom: 20px;
  }
  .link-btn {
    width: 100%;
  }
}
html, body{
  padding: 0;
  margin: 0;
}

.App {
  text-align: center;
  flex-direction: column;
  display:flex;
  align-items: center;
  justify-content: center;
  /* min-height: 100vh; */
}

